<template>
  <div id="addFlow" >
    <el-drawer v-model="drawer" :direction="direction" :before-close="cancelClick">
      <template #header>
        <div class="titleBox">
          {{ $t('ClienteleQuery.reply') }}
        </div>
      </template>
      <template #default>
        <div>
          <el-form ref="replyForm" :rules="replyRules" :model="form" label-width="80px">
            <el-form-item :label="$t('ClienteleQuery.afterOrderNumber')">
              <el-input v-model="form.afterOrderNumber" :readonly="true"></el-input>
            </el-form-item>
            <el-form-item :label="$t('ClienteleQuery.formTitle')" prop="title">
              <el-input v-model="form.title" :placeholder="$t('ClienteleQuery.formTitlePlaceholder')"></el-input>
            </el-form-item>
            <el-form-item :label="$t('ClienteleQuery.formDescribe')" prop="content">
              <el-input v-model="form.content" :rows="5" type="textarea" :placeholder="$t('ClienteleQuery.formDescribePlaceholder')"/>
            </el-form-item>
            <el-divider content-position="left">{{ $t('ClienteleQuery.uploadTitle') }}</el-divider>
            <div class="imgdiv">
              <el-upload
                  v-model:file-list="form.fileList"
                  action="/api/clientele/uploadFileClientele"
                  :data="fileData" list-type="picture-card"
                  :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload"
                  :on-preview="handlePictureCardPreview" :on-remove="handleRemove"
              >
                <el-icon><Plus /></el-icon>
              </el-upload>

              <el-dialog v-model="dialogVisible">
                <img w-full :src="dialogImageUrl" alt="Preview Image" />
              </el-dialog>
            </div>
          </el-form>
        </div>
      </template>
      <template #footer>
        <div style="flex: auto">
          <el-button @click="cancelClick">
            {{ $t('common.button.cancel') }}
          </el-button>
          <el-button type="primary" @click="confirmClick"> {{ $t('common.button.confirm') }} </el-button>
        </div>
      </template>
    </el-drawer>  </div>
</template>

<script>

import {saveAfterFlowClientele} from "@/http/clienteleApi";
import {ElLoading, ElNotification} from "element-plus";

export default {

  name: "addFlow",
  components: {},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      drawer: false,
      direction: 'rtl',
      form: {
        fileList: [],
      },
      // 回复框 - 表单验证规则
      replyRules: {
        title: [
          {required: true, message: this.$t('ClienteleQuery.formTitlePlaceholder'), trigger: 'blur'},
        ],
        content: [
          {required: true, message: this.$t('ClienteleQuery.formDescribePlaceholder'), trigger: 'blur'},
        ],
      },
      dialogImageUrl: '',
      dialogVisible: false,
      // 请求接口需要携带的参数
      fileData: {
        folder: 'flow-img'
      },
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },
  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 上传之后的res结果集
    handleAvatarSuccess(res, file) {
      if (res.status == 200) {
        // vue.config.js 定义了后端请求前缀路径 可直接调用
        this.imageUrl = process.env.VUE_APP_BACKEND_API_URL + res.data;
        console.log(this.imageUrl, 'this.imageUrl')
      } else {
        this.$message.error(this.$t('common.message.networkError'));
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadError'), type: 'info',})
      }
      if (!isLt2M) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadSizeError'), type: 'info',})
      }
      return isJPG && isLt2M;
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },

    // 取消按钮
    cancelClick() {
      this.drawer = false;
      this.form = {};
      this.$refs.replyForm.resetFields(); // 重置表单验证
    },
    // 确认按钮
    confirmClick() {
      // 提交校验
      this.$refs.replyForm.validate((valid) => {
        if (valid) {
          const loading = ElLoading.service({
            lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
          );
          if (this.form.fileList && this.form.fileList.length > 0) {
            let imgs = [];
            for (let i = 0; i < this.form.fileList.length; i++) {
              imgs.push(this.form.fileList[i].response.data);
            }
            this.form.imgs = imgs;
          }
          this.form.createStatus = 1;
          // 提交表单
          saveAfterFlowClientele(this.form).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',});
              this.drawer = false;
              this.$emit('childClick');
              this.form = {};
            }
          }).finally(() => {
            // 请求无论成功/失败 都执行关闭loading
            loading.close();
          });
        } else {
          return false;
        }
      });
    },
  },
}
</script>


<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.imgdiv {
  height: 450px;
  width: 100%;
  border: 1px solid #ccc;
  overflow: auto;
}
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}
.titleBox {
  font-size: 18px;
  font-weight: bold;
  color: #333333;
}
</style>
