<template>
  <div id="clienteleVeiw" class="clienteleVeiw" >
    <div class="common-layout">
      <el-container>
        <el-header class="header">
          <el-row :gutter="20" style="height: 100%">
            <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
            <el-col :span="14"><div class="grid-content ep-bg-purple" />
              <div class="headerBox">
                <!--                <el-button type="primary" plain @click="active = 3">步骤四</el-button>-->
                <!--                <span>{{ $t('Clientele.header') }}</span>-->
                <div><img src="../../assets/ifi-logo.png" alt="ifiLogo" style="width: 30px;"></div>
                <div style="margin-left: 10px">{{ $t('Clientele.header') }}</div>
                <div class="linkBox">
                  <!-- 主题_选项 -->
                  <el-switch
                      class="themeBox"
                      v-model="theme"
                      active-action-icon="Watermelon"
                      inactive-action-icon="MagicStick"
                      @change="themeSwitchChange()"
                  />
                  <el-dropdown @command="switchover" >
                  <span class="el-dropdown-link">
                    <svg class="linkSvg" preserveAspectRatio="xMidYMid meet" viewBox="0 0 24 24" width="1.2em" height="1.2em" data-v-12008bb2=""><path fill="currentColor" d="m18.5 10l4.4 11h-2.155l-1.201-3h-4.09l-1.199 3h-2.154L16.5 10h2zM10 2v2h6v2h-1.968a18.222 18.222 0 0 1-3.62 6.301a14.864 14.864 0 0 0 2.336 1.707l-.751 1.878A17.015 17.015 0 0 1 9 13.725a16.676 16.676 0 0 1-6.201 3.548l-.536-1.929a14.7 14.7 0 0 0 5.327-3.042A18.078 18.078 0 0 1 4.767 8h2.24A16.032 16.032 0 0 0 9 10.877a16.165 16.165 0 0 0 2.91-4.876L2 6V4h6V2h2zm7.5 10.885L16.253 16h2.492L17.5 12.885z"></path></svg>
                  </span>
                    <template #dropdown>
                      <el-dropdown-menu>
                        <el-dropdown-item command="zhCn">
                          <el-icon><Football /></el-icon> {{ $t("HomeView.language.zh") }}
                        </el-dropdown-item>
                        <el-dropdown-item command="en">
                          <el-icon><Help /></el-icon> {{ $t("HomeView.language.en") }}
                        </el-dropdown-item>
                      </el-dropdown-menu>
                    </template>
                  </el-dropdown>
                </div>
              </div>
            </el-col>
            <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
          </el-row>
        </el-header>
        <el-main>
          <el-row style="height: 100%">
            <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
            <el-col :span="14" ><div class="grid-content ep-bg-purple-light"/>
              <div class="inpbox">
                <el-form :inline="true" :model="fromData" class="demo-form-inline" :label-width="100" label-position="right">
                  <el-form-item :label="$t('ClienteleQuery.afterOrderNumber')">
                    <el-input v-model="fromData.afterOrderNumber" :readonly="true"></el-input>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="listData()" icon="Search"> {{ $t('common.button.search') }} </el-button>
                  </el-form-item>
                  <el-form-item>
                    <div class="scroll-container">
                      <div v-if="MessagePushList.length > 0" class="scroll-item" :key="currentIndex">
                        <span style="font-weight: bold; color: #dc2c2c">{{ MessagePushList[currentIndex].title }}</span>
                        :
                        <span style="color: #858181">
                          {{ MessagePushList[currentIndex].content }}
                        </span>
                      </div>
                    </div>
                  </el-form-item>
                </el-form>
              </div>

              <div style="height: 90%; margin-top: 5px;" >
                <el-row>
                  <el-col :span="6">
                    <div class="grid-content bg-purple spubox">
                      <div class="spuboxcard">
                        <el-card :body-style="{ padding: '0px' }" style="width: 99%; height: 100%; margin-bottom: 5px;" shadow="hover">
                          <div class="imgbox">
                            <img :src="getUrl(afterDate.spuImg)" class="image">
                          </div>
                          <div style="padding: 14px">
                            <div class="titlebox">{{ $t('ClienteleQuery.spuName') }}: {{ afterDate.spuName }}</div>
                            <div class="time">{{ $t('ClienteleQuery.function') }}: {{ afterDate.functionName }}</div>
                            <div class="time">{{ $t('ClienteleQuery.series') }}: {{ afterDate.seriesName }}</div>
                          </div>
                          <div style="padding: 14px">
                            <el-divider content-position="right">{{ $t('ClienteleQuery.divider') }}</el-divider>
                            <div class="titlebox">{{ $t('ClienteleQuery.problemTitle') }}: {{ afterDate.problemTitle }}</div>
                            <div class="time">{{ $t('ClienteleQuery.name') }}: {{ afterDate.familyName }}</div>
                            <div class="time">{{ $t('ClienteleQuery.email') }}: {{ afterDate.email }}</div>
                            <div class="time">{{ $t('ClienteleQuery.function') }}: {{ afterDate.spuSequence }}</div>
                            <div class="time">{{ $t('ClienteleQuery.spuSequence') }}: {{ afterDate.buyTime }}</div>
                            <div class="time">{{ $t('ClienteleQuery.buyTime') }}: {{ afterDate.buyDitchName }}</div>
                            <div class="time">{{ $t('ClienteleQuery.buyDitchName') }}: {{ afterDate.ditchRemark }}</div>
                            <div class="time">{{ $t('ClienteleQuery.region') }}: {{ afterDate.regionName }}</div>
                            <div class="time">{{ $t('ClienteleQuery.summary') }}: {{ afterDate.summary }}</div>
                            <div class="time">{{ $t('ClienteleQuery.detail') }}: {{ afterDate.detail }}</div>
                            <div class="time">{{ $t('ClienteleQuery.afterStatus') }}:
                              {{
                                afterDate.afterStatus == 1 ? this.$t('pullDown.afterStatus.allocation')
                                : afterDate.afterStatus == 2 ? this.$t('pullDown.afterStatus.pending')
                                : afterDate.afterStatus == 3 ? this.$t('pullDown.afterStatus.processing')
                                : afterDate.afterStatus == 4 ? this.$t('pullDown.afterStatus.completed')
                                : this.$t('pullDown.unknown')
                              }}
                            </div>
                            <div class="time">{{ $t('ClienteleQuery.disposeUser') }}: {{ afterDate.disposeUserNmae ? afterDate.disposeUserNmae : this.$t('pullDown.noData') }}</div>
                            <div class="time">{{ $t('ClienteleQuery.createTime') }}: {{ afterDate.createTime }}</div>
                          </div>
                        </el-card>
                      </div>
                    </div>
                  </el-col>
                  <el-col :span="18">
                    <div class="grid-content bg-purple-light frombox">
                      <el-card class="box-card" shadow="hover">
                        <template #header>
                          <div class="card-header">
                            <span>{{ $t('ClienteleQuery.flow') }}</span>
                            <div style="float: right">
                              <el-button type="success" plain @click="goToAdd()" v-if="buis" v-show="afterDate.afterStatus != 4">
                                {{ $t('common.button.reply') }}
                              </el-button>
                              <el-button type="danger" plain @click="updateAfterStatus()" v-if="buis" v-show="afterDate.afterStatus != 4">
                                {{ $t('common.button.end') }}
                              </el-button>
                            </div>
                          </div>
                        </template>
                        <!-- 表单部分 -->
                        <div class="text item">
                          <div class="block">
                            <el-timeline :reverse="false">
                              <el-timeline-item v-for="item in afterFlowVoList" :key="item.id"  :timestamp="newStr(item)" placement="top">
                                <el-card shadow="hover">
                                  <div class="titlebox">
                                    {{ $t('ClienteleQuery.formTitle') }}: {{ item.title }}
                                    <div class="fromBox" >
                                      <el-button v-if="item.affairId != null && item.affairDetailId == null" @click="addAffair(item)" key="primary" type="primary" link>
                                        {{ $t('common.button.fillIn') }}
                                      </el-button>
                                      <el-button v-if="item.affairDetailId != null" @click="getAffair(item.affairDetailId)" key="primary" type="primary" link>
                                        {{ $t('common.button.check') }}
                                      </el-button>
                                      <el-button v-if="item.ditchStatus == 'LCZT-004' && item.expressageStatus == 1" @click="gotoSignFor(item)" key="primary" type="primary" link>
                                        {{ $t('common.button.sign') }}
                                      </el-button>
                                    </div>
                                  </div>
                                  <div class="time"><b>{{ $t('ClienteleQuery.formDescribe') }}: </b>{{ item.content }}</div>
                                  <div class="demo-image__preview" v-if="item.imgs != null">
                                    <el-divider content-position="right">{{ $t('ClienteleQuery.reference') }}</el-divider>
                                    <el-image v-for="img in item.imgs" :key="img"
                                        style="width: 85px; height: 85px; margin-right: 5px; margin-bottom: 5px;"
                                        :src="getUrl(img)"
                                        :zoom-rate="1.2"
                                        :max-scale="7"
                                        :min-scale="0.2"
                                        :preview-src-list="addurl(item.imgs)"
                                        :initial-index="4"
                                        fit="cover"
                                    />
                                  </div>
                                </el-card>
                              </el-timeline-item>
                            </el-timeline>
                          </div>
                        </div>
                        <template #footer class="dibutxt">

                        </template>
                      </el-card>
                    </div>
                  </el-col>
                </el-row>
              </div>
            </el-col>
            <el-col :span="5"><div class="grid-content ep-bg-purple" />

            </el-col>
          </el-row>
        </el-main>
      </el-container>
    </div>
    <!-- 用户填入 -->
    <el-dialog v-model="newAffairVisible" :before-close="beforeClose" :title="$t('ClienteleQuery.fillInLogistics')" width="750" draggable>
      <el-form :rules="rules" ref="ruleForm" :model="newAffairForm" label-width="80px">
        <el-form-item :label="$t('ClienteleQuery.afterOrderNumber')">
          <el-input v-model="newAffairForm.afterOrderNumber" :readonly="true"></el-input>
        </el-form-item>
        <el-form-item :label="$t('ClienteleQuery.ditchStatus')" prop="ditchStatus">
          <el-select filterable v-model="newAffairForm.ditchStatus" :placeholder="$t('ClienteleQuery.ditchStatusPlaceholder')" :disabled="true" style="width: 100%">
            <el-option v-for="item in selectData.ditchStatusList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('ClienteleQuery.logistics')" prop="expressage" v-if="signForVisible">
          <el-input v-model="newAffairForm.expressage" :placeholder="$t('ClienteleQuery.logisticsPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('ClienteleQuery.logisticsNumber')" prop="expressageNumber" v-if="signForVisible">
          <el-input v-model="newAffairForm.expressageNumber" :placeholder="$t('ClienteleQuery.logisticsNumberPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('ClienteleQuery.sendoutDate')" prop="sendoutDate" v-if="signForVisible">
          <el-date-picker v-model="newAffairForm.sendoutDate" :placeholder="$t('ClienteleQuery.sendoutDatePlaceholder')" :disabled="isReadonly" type="date" format="YYYY-MM-DD" value-format="YYYY-MM-DD" style="width: 100%"/>
        </el-form-item>
        <el-form-item :label="$t('ClienteleQuery.signDate')" prop="sendoutDate" v-if="!signForVisible">
          <el-date-picker v-model="newAffairForm.sendoutDate" :placeholder="$t('ClienteleQuery.signDatePlaceholder')" :disabled="isReadonly" type="date" format="YYYY-MM-DD" value-format="YYYY-MM-DD" style="width: 100%"/>
        </el-form-item>
        <el-form-item :label="$t('ClienteleQuery.remarks')">
          <el-input type="textarea" v-model="newAffairForm.remarks" :placeholder="$t('ClienteleQuery.remarksPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="newAffairClick">{{ $t('common.button.back') }}</el-button>
        <el-button type="primary" @click="newAffairAdd" v-if="!isReadonly && signForVisible">{{ $t('common.button.submit') }}</el-button>
        <el-button type="primary" @click="newsignFor" v-if="!signForVisible">{{ $t('common.button.sign') }}</el-button>
      </span>
      </template>
    </el-dialog>

    <!-- 用户回复 -->
    <addFlow ref="addFlow" @childClick="listData"/>
  </div>
</template>

<script>
import {
  finishAfterClientele,
  getAffairDetailByIdClientele,
  getDictPullDownClientele,
  listAfterFlowBySelcetUrlClientele,
  listMessagePushClientele,
  saveAffairClientele,
  signForClientele
} from "@/http/clienteleApi";
import {ElLoading, ElNotification} from "element-plus";
import addFlow from "@/clientView/after-flow/add-flow";
import en from 'element-plus/dist/locale/en.mjs'
import zhCn from 'element-plus/dist/locale/zh-cn.mjs'
import {getSysTheme} from "@/util/system";

export default {

  name: "clienteleVeiw",
  components: {addFlow},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      theme: false,

      fromData: {}, // 查询条件
      afterDate: {}, // 售后单信息 查询获取
      afterFlowVoList: {}, // 产品信息

      buis: false, // 是否显示按钮

      MessagePushList: [], // 消息推送列表
      currentIndex: 0,

      newAffairVisible: false,
      isReadonly: false,
      selectData: {
        ditchStatusList: [],
      },
      newAffairForm: {
        affairId: 0,
        affairDetailId: 0,
        afterOrderNumber: '',
        ditchStatus: 'LCZT-002',
        expressage: '',
        expressageNumber: '',
        sendoutDate: '',
        remarks: '',
      },
      // 表单验证规则
      rules: {
        ditchStatus: [
          {required: true, message: this.$t('ClienteleQuery.ditchStatusPlaceholder'), trigger: 'change'},
        ],
        expressage: [
          {required: true, message: this.$t('ClienteleQuery.logisticsPlaceholder'), trigger: 'blur'},
        ],
        expressageNumber: [
          {required: true, message: this.$t('ClienteleQuery.logisticsNumberPlaceholder'), trigger: 'blur'},
        ],
        sendoutDate: [
          {required: true, message: this.$t('ClienteleQuery.sendoutDatePlaceholder'), trigger: 'change'},
        ],
      },
      // 签收表单隐藏
      signForVisible: true,
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getViewData();
    this.startAutoScroll();
    this.setTheme();
  },
  // methods: 对象中的方法，可以在页面中调用
  methods: {
    // 本地主题开关
    setTheme() {
      this.theme = getSysTheme();
    },
    // 主题开关
    themeSwitchChange() {
      // theme 主题开关 true=暗黑 false=明亮
      window.localStorage.setItem("themeSwitch", this.theme);
      // 调用父组件的方法
      this.$emit('childClick', 'theme');
      // 刷新网页
      location.reload();
    },
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },
    addurl(imgs) {
      if (!imgs) return [];
      let arr = [];
      imgs.forEach(item => {
        arr.push(this.getUrl(item));
      })
      return arr;
    },
    newStr(item) {
      return (item.createStatus == 1 ? this.$t('AfterSpu.flow.user') + ' ' + item.familyName : this.$t('AfterSpu.flow.service') + item.disposeUserNmae) + ' ' + item.createTime
    },
    getViewData() {
      listMessagePushClientele().then(res => {
        if (res.data.status == 200) {
          this.MessagePushList = res.data.data;
        }
      }).catch((erro) => { this.reset(); });
      // 获取url中的工单号
      let urlselcetUrl = this.$route.params.selcetUrl
      if (urlselcetUrl) {
        this.fromData.selcetUrl = urlselcetUrl;
        this.listData();
        return;
      }
      // 获取本地的工单号用于查询 / 反之不查询
      let selcetUrl = localStorage.getItem("addAfterOrderNumberClientele");
      if (selcetUrl) {
        this.fromData.selcetUrl = selcetUrl;
        this.listData();
      }
    },
    // 列表数据请求
    listData() {
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      if (!this.fromData.selcetUrl) {
        ElNotification({title: this.$t('common.dialog.title'), message: this.$t('ClienteleQuery.queryNull'), type: 'error',})
        return;
      }
      listAfterFlowBySelcetUrlClientele(this.fromData).then(res => {
        if (res.data.status == 200) {
          this.fromData.afterOrderNumber = res.data.data.afterVo.afterOrderNumber;
          this.afterDate = res.data.data.afterVo;
          this.afterFlowVoList = res.data.data.afterFlowVoList;
          this.buis = true;
        } else{
          this.reset();
        }
      }).catch((erro) => {
        this.reset();
      }).finally(() => {
        // 请求无论成功/失败 都执行关闭loading
        loading.close();
      });
    },
    reset () {
      this.buis = false;
      this.afterDate = {}; // 售后单信息 查询获取
      this.afterFlowVoList = {}; // 产品信息
    },
    // 打开回复弹窗
    goToAdd() {
      this.$nextTick(() => {
        this.$refs.addFlow.drawer = true;
        this.$refs.addFlow.form.afterOrderNumber = this.afterDate.afterOrderNumber;
      }, 100);
    },
    // 结束售后工单
    updateAfterStatus() {
      this.$confirm(this.$t('ClienteleQuery.finishTxt'), this.$t('common.dialog.title'),
          {confirmButtonText: this.$t('common.button.confirm'), cancelButtonText: this.$t('common.button.cancel'), type: 'warning'}
      ).then(() => {
        // 结束售后工单请求
        finishAfterClientele(this.afterDate).then(res => {
          if (res.data.status == 200) {
            this.afterDate.afterStatus = 4;
            ElNotification({title: this.$t('message.successTitle'), message: this.$t('ClienteleQuery.afterEnd'), type: 'success',})
          }
        }).catch(error => { });
      }).catch(() => { });
    },
    startAutoScroll() {
      setInterval(() => {
        if (this.currentIndex == this.MessagePushList.length - 1) {
          this.currentIndex = 0;
        } else {
          this.currentIndex++;
        }
      }, 5000);
    },

    getaffData() {
      // 事件状态
      getDictPullDownClientele({dictCode: 'LCZT'}).then(res => {
        if (res.data.status == 200) this.selectData.ditchStatusList = res.data.data;
      }).catch((erro) => { });
      this.newAffairForm.afterOrderNumber = this.afterDate.afterOrderNumber;
      this.newAffairVisible = true;
    },
    // 打开填入物流弹窗
    addAffair(item) {
      this.newAffairForm.affairId = item.affairId;
      this.newAffairForm.flowId = item.id;
      this.getaffData();
    },
    // 打开查看物流弹窗
    getAffair(affairDetailId) {
      this.newAffairForm.affairDetailId = affairDetailId;
      this.getaffData();
      this.isReadonly = true;
      // 根据id查询物流信息
      getAffairDetailByIdClientele({affairDetailId: affairDetailId}).then(res => {
        if (res.data.status == 200) {
          this.newAffairForm = res.data.data;
          this.newAffairForm.afterOrderNumber = this.afterDate.afterOrderNumber;
        }
      }).catch((erro) => { });
    },
    // 打开签收弹窗
    gotoSignFor(item) {
      this.getaffData();
      this.isReadonly = false;
      this.signForVisible = false;
      this.newAffairForm.affairDetailId = item.affairDetailId;
      this.newAffairForm.affairId = item.affairId;
      this.newAffairForm.ditchStatus = 'LCZT-005';
      this.newAffairForm.expressage = null;
      this.newAffairForm.expressageNumber = null;
    },
    // 签收
    newsignFor() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          signForClientele(this.newAffairForm).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',});
              this.newAffairClick();
              this.listData();
            }
          }).catch((erro) => { });
        } else {
          return false;
        }
      });
    },
    // 提交用户的物流信息
    newAffairAdd() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.newAffairForm.expressageStatus = 1;
          this.newAffairForm.createStatus = 1;
          saveAffairClientele(this.newAffairForm).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',});
              this.newAffairClick();
              this.listData();
            }
          }).catch((erro) => { });
        } else {
          return false;
        }
      });
    },
    // 点击遮罩和x时执行
    beforeClose(done) {
      this.newAffairClick();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 关闭
    newAffairClick() {
      this.newAffairVisible = false;
      this.isReadonly = false;
      this.newAffairForm = {
        affairId: 0,
        affairDetailId: 0,
        afterOrderNumber: '',
        ditchStatus: 'LCZT-002',
        expressage: '',
        expressageNumber: '',
        sendoutDate: '',
        remarks: '',
      };
      this.$refs.ruleForm.resetFields(); // 重置表单验证
      // 延迟1秒执行
      setTimeout(() => {this.signForVisible = true;}, 300)
    },
    // 子组件调用父组件的方法 切换语言 值必须是语言文件的引入名称
    switchover(command) {
      if (command == 'en') {
        this.$confirm(this.$t("HomeView.languageDialog.enContent"), this.$t("HomeView.languageDialog.title"), {
          confirmButtonText: this.$t("common.button.confirm"),
          cancelButtonText: this.$t("common.button.cancel"),
          type: 'success'
        }).then(() => {
          this.gotolanguage('en');
        });
      } else {
        this.$confirm(this.$t("HomeView.languageDialog.zhContent"), this.$t("HomeView.languageDialog.title"), {
          confirmButtonText: this.$t("common.button.confirm"),
          cancelButtonText: this.$t("common.button.cancel"),
          type: 'success'
        }).then(() => {
          this.gotolanguage('zh');
        });
      }
    },
    // 切换语言 刷新首页框架数据 重新获取菜单 重新获取标签 重新获取标题 ...
    gotolanguage(locale) {
      this.$i18n.locale = locale; // 切换 I18n 语言包 选项式写法
      if (locale == 'en') {
        this.$emit('childClick', en);
        window.localStorage.setItem("navigatorLanguage", 'en-US');
      } else if (locale == 'zh'){
        this.$emit('childClick', zhCn);
        window.localStorage.setItem("navigatorLanguage", 'zh-CN');
      }
      // 刷新网页
      location.reload();
    },
  },
}
</script>


<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.clienteleVeiw {
  height: 100%;
}
.common-layout{
  height: 100%;
}

.header {
  height: 7vh;
}
.main {
  height: 86vh;
  /*border: 1px solid #000;*/
